<template>
  <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-tickets"></i> Song information
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          size="mini"
          class="handle-del mr10"
          @click="delAll"
          >Batch delete</el-button
        >
        <el-input
          v-model="select_word"
          size="mini"
          placeholder="Select keywords"
          class="handle-input mr10"
        ></el-input>
        <el-button
          type="primary"
          size="mini"
          @click="centerDialogVisible = true"
          >Add songs</el-button
        >
      </div>
      <el-table
        :data="data"
        size="mini"
        border
        style="width: 100%"
        ref="multipleTable"
        height="550px"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="40"></el-table-column>
        <el-table-column label="Singer images" width="110" align="center">
          <template slot-scope="scope">
            <div style="width: 80px; height: 80px; overflow: hidden">
              <img :src="getUrl(scope.row.pic)" alt="" style="width: 100%;" />
            </div>
            <div
              class="play"
              @click="setSongUrl(scope.row.url, scope.row.name)"
            >
              <div v-if="toggle !== scope.row.name">
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="BOFANG"></use>
                </svg>
              </div>
              <div v-if="toggle === scope.row.name">
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="playIcon"></use>
                </svg>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label="Song"
          prop="name"
          width="150"
          align="center"
        ></el-table-column>
        <el-table-column
          label="Album"
          prop="introduction"
          width="150"
          align="center"
        ></el-table-column>
        <el-table-column label="Lyric" align="center">
          <template slot-scope="scope">
            <ul style="height: 100px; overflow: scroll">
              <li></li>
              <li
                v-for="(item, index) in parseLyric(scope.row.lyric)"
                :key="index"
              >
                {{ item }}
              </li>
            </ul>
          </template>
        </el-table-column>
        <el-table-column label="Resource update" width="100" align="center">
          <template slot-scope="scope">
            <el-upload
              class="upload-demo"
              :action="uploadUrl(scope.row.id)"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <el-button size="mini">Update image</el-button>
            </el-upload>
            <br />
            <el-upload
              class="upload-demo change"
              :action="uploadSongUrl(scope.row.id)"
              :show-file-list="false"
              :on-success="handleSongSuccess"
              :before-upload="beforeSongUpload"
            >
              <el-button size="mini">Update song</el-button>
            </el-upload>
          </template>
        </el-table-column>
        <el-table-column label="Comment" width="80" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="getComment(data[scope.$index].id)"
              >comment</el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="Operation" width="150" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >Modify</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row.id)"
              >Delete</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="total, prev, pager, next"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
    </div>

    <el-dialog
      title="Add song"
      :visible.sync="centerDialogVisible"
      width="400px"
      center
    >
      <el-form action="" :model="registerForm" id="tf">
        <div>
          <label>Song</label>
          <el-input type="text" name="name"></el-input>
        </div>
        <div>
          <label>Album</label>
          <el-input type="text" value="" name="introduction"></el-input>
        </div>
        <div>
          <label>Lyric</label>
          <el-input type="textarea" value="" name="lyric"></el-input>
        </div>
        <div>
          <label>Upload-song</label>
          <br />
          <input type="file" name="file" id="update-file-input" />
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="addSong">OK</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Modify" :visible.sync="editVisible" width="600px">
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="Singer-song" size="mini">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="Introduction" size="mini">
          <el-input v-model="form.introduction"></el-input>
        </el-form-item>
        <el-form-item label="Lyric" size="mini">
          <el-input type="textarea" v-model="form.lyric"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="editVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="saveEdit">OK</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Hint" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt" align="center">
        Delete is unrecoverable, is it determined to delete?
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="delVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="deleteRow"
          >Delete</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import SongAudio from "../components/SongAudio";
import { mixin } from "../mixins";
import { mapGetters } from "vuex";
import { ICON } from "../assets/icon/index";
import { HttpManager } from "../api/index";

export default {
  name: "song-page",
  components: {
    SongAudio
  },
  mixins: [mixin],
  data() {
    return {
      toggle: false,
      singerId: "",
      singerName: "",
      registerForm: {
        name: "",
        singerName: "",
        introduction: "",
        lyric: ""
      },
      tableData: [],
      tempDate: [],
      is_search: false,
      multipleSelection: [],
      centerDialogVisible: false,
      editVisible: false,
      delVisible: false,
      select_word: "",
      form: {
        id: "",
        singerId: "",
        name: "",
        introduction: "",
        createTime: "",
        updateTime: "",
        pic: "",
        lyric: "",
        url: ""
      },
      pageSize: 5,
      currentPage: 1,
      idx: -1,
      BOFANG: ICON.BOFANG,
      ZANTING: ICON.ZANTING
    };
  },
  computed: {
    ...mapGetters(["isPlay"]),

    data() {
      return this.tableData.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    },
    playIcon() {
      if (this.isPlay) {
        return this.ZANTING;
      } else {
        return this.BOFANG;
      }
    }
  },
  watch: {
    select_word: function() {
      if (this.select_word === "") {
        this.tableData = this.tempDate;
      } else {
        this.tableData = [];
        for (let item of this.tempDate) {
          if (item.name.includes(this.select_word)) {
            this.tableData.push(item);
          }
        }
      }
    }
  },
  created() {
    this.singerId = this.$route.query.id;
    this.singerName = this.$route.query.name;
    this.getData();
  },
  destroyed() {
    this.$store.commit("setIsPlay", false);
  },
  methods: {
    getData() {
      this.tableData = [];
      this.tempDate = [];
      HttpManager.getSongOfSingerId(this.singerId)
        .then(res => {
          this.tableData = res;
          this.tempDate = res;
          this.currentPage = 1;
        })
        .catch(err => {
          console.log(err);
        });
    },
    setSongUrl(url, name) {
      this.$store.commit("setUrl", this.$store.state.HOST + url);
      this.toggle = name;
      if (this.isPlay) {
        this.$store.commit("setIsPlay", false);
      } else {
        this.$store.commit("setIsPlay", true);
      }
    },

    uploadUrl(id) {
      return `${this.$store.state.HOST}/song/img/update?id=${id}`;
    },

    uploadSongUrl(id) {
      return `${this.$store.state.HOST}/song/url/update?id=${id}`;
    },
    beforeSongUpload(file) {
      var testmsg = file.name.substring(file.name.lastIndexOf(".") + 1);
      const extension = testmsg === "mp3";
      if (!extension) {
        this.$message({
          message: "上传文件只能是mp3格式！",
          type: "error"
        });
      }
      return extension;
    },

    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleSongSuccess(res, file) {
      if (res.code === 1) {
        this.getData();
        this.notify("上传成功", "success");
      } else {
        this.notify("上传失败", "error");
      }
    },

    addSong() {
      let _this = this;
      var form = new FormData(document.getElementById("tf"));
      form.append("singerId", this.singerId);
      form.set("name", this.singerName + "-" + form.get("name"));
      if (!form.get("lyric")) {
        form.set("lyric", "[00:00:00]暂无歌词");
      }
      var req = new XMLHttpRequest();
      req.onreadystatechange = function() {
        if (req.readyState === 4 && req.status === 200) {
          let res = JSON.parse(req.response);
          if (res.code) {
            _this.getData();
            _this.registerForm = {};
            _this.notify(res.msg, "success");
          } else if (!res.code) {
            _this.notify("上传失败", "error");
          }
        }
      };
      req.open("post", `${_this.$store.state.HOST}/song/add`, false);
      req.send(form);
      _this.centerDialogVisible = false;
    },

    handleEdit(row) {
      this.idx = row.id;
      this.form = {
        id: row.id,
        singerId: row.singerId,
        name: row.name,
        introduction: row.introduction,
        createTime: row.createTime,
        updateTime: row.updateTime,
        pic: row.pic,
        lyric: row.lyric,
        url: row.url
      };
      this.editVisible = true;
    },
    getComment(id) {
      this.$router.push({ path: "/Comment", query: { id: id, type: 0 } });
    },

    saveEdit() {
      let params = new URLSearchParams();
      params.append("id", this.form.id);
      params.append("singerId", this.form.singerId);
      params.append("name", this.form.name);
      params.append("introduction", this.form.introduction);
      params.append("lyric", this.form.lyric);
      HttpManager.updateSongMsg(params)
        .then(res => {
          if (res) {
            this.getData();
            this.notify("Modified successfully", "success");
          } else {
            this.notify("Failed to modify", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.editVisible = false;
    },

    deleteRow() {
      HttpManager.deleteSong(this.idx)
        .then(response => {
          if (response) {
            this.getData();
            this.notify("Deleted successfully", "success");
          } else {
            this.notify("Failed to delete", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.delVisible = false;
    },
    parseLyric(text) {
      let lines = text.split("\n");
      let pattern = /\[\d{2}:\d{2}.(\d{3}|\d{2})\]/g;
      let result = [];

      if (!/\[.+\]/.test(text)) {
        return [text];
      }
      for (let item of lines) {
        if (pattern.test(item)) {
          let value = item.replace(pattern, "");
          result.push(value);
        }
      }
      return result;
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}
.handle-input {
  width: 300px;
  display: inline-block;
}
.el-input__inner {
  background-color: aqua;
}
.play {
  position: absolute;
  z-index: 100;
  width: 80px;
  height: 80px;
  top: 18px;
  left: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.icon {
  width: 2em;
  height: 2em;
  color: white;
  fill: currentColor;
  overflow: hidden;
}
.pagination {
  display: flex;
  justify-content: center;
}
</style>



