<template>
  <div class="song-audio">
    <audio
      id="player"
      controls="controls"
      preload="true"
      :src="url"
      @canplay="startPlay"
      @ended="ended"
    ></audio>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "song-audio",
  computed: {
    ...mapGetters(["id", "url", "isPlay"])
  },
  watch: {
    isPlay: function() {
      this.togglePlay();
    }
  },
  methods: {
    togglePlay() {
      let player = document.querySelector("#player");
      if (this.isPlay) {
        player.play();
      } else {
        player.pause();
      }
    },
    startPlay() {
      let player = document.querySelector("#player");
      player.play();
    },
    ended() {
      this.isPlay = false;
    }
  }
};
</script>

<style>
#player {
  display: none;
}
</style>



